<template>
  <div class="wiki-active-warp">
    <div class="wiki-active">
      <div class="wiki-active-content">
        <div class="active-content-email">
          邮箱：
          <el-form
            :model="dynamicValidateForm"
            ref="dynamicValidateForm"
            label-width="100px"
            class="demo-dynamic"
          >
            <el-form-item
              prop="email"
              :rules="[
                        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                        { type: 'email', message: '请输入正确的邮箱格式', trigger: 'change' }
                    ]"
              class="wiki-modify-main-select-email"
            >
              <el-input v-model="dynamicValidateForm.email"></el-input>
            </el-form-item>
          </el-form>
        </div>

        <input type="password" v-model="password" class="active-content-password" placeholder="请输入密码"/>

        <a href="#/login" class="active-content-go-login">已有账号？登录</a>
        <el-button type="primary" @click="handleActive">发送激活邮件</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import * as ajax from "@/api"; //请求

export default {
  data() {
    return {
      password: "",
      ruleForm2: {
        pass: "",
        checkPass: ""
      },
      errorMessage: "",
      dynamicValidateForm: {
        domains: [
          {
            value: ""
          }
        ],
        email: ""
      }
    };
  },
  methods: {
    handleActive() {
      var reg = new RegExp(
        "^[a-z0-9A-Z]+[- | a-z0-9A-Z . _]+@([a-z0-9A-Z]+(-[a-z0-9A-Z]+)?\\.)+[a-z]{2,}$"
      );

      if (this.password == "") {
        alert("密码不能为空");
        return;
      } else if (this.dynamicValidateForm.email == "") {
        alert("邮箱为空，请输入邮箱");
        return;
      } else if (!reg.test(this.dynamicValidateForm.email)) {
        alert("邮箱格式不对，请输入正确的邮箱！");
        return;
      } else {
        ajax.handleActive(
            this.password,this.dynamicValidateForm.email
          )
          .then(resp => {
            if (resp.data.code) {
              this.$message("发送成功，请在10分钟之内查看邮件激活！");
              this.$router.push("/login");
            } else {
              this.errorMessage = resp.data.msg;
              this.$message({
                    showClose: true,
                    message: msg,
                    type: "error"
                });
            }
          })
      }
    }
  }
};
</script>

